import styles from './Button.module.css';

const Button = ({text, link}) => {
    return (
      <button type="button" className={styles.button}>
       <a target="_blank" rel="noreferrer noopener" href={link}>
         {text}</a> 
      </button>
    );
  }

export default Button;

